.input {
    height: 50px;
    width: 80%;
    border-radius: 12px;
    background-color: aliceblue;
    display: flex;
    justify-content: center;
    align-items: center;
}

input {
    background: none;
    border: none;
    margin-left: 12px;
    flex: 1;
}

input:focus {
    outline: none;
}

button {
    margin-right: 8px;
    background-color: rgba(214, 213, 213, 0.75);
    border: none;
    border-radius: 10px;
    height: 30px;
    width: 50px;
}