* {
  margin: 0;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.app {
  min-height: 100vh;
  height: auto;
  width: 100vw;
  background-color: rgb(187, 212, 212);
  display: grid;
  place-items: center;
}

.appContainer {
  min-height: 60%;
  height: auto;
  width: 50%;
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: rgb(232, 234, 237);
  border-radius: 12px;
  padding: 32px 0;
  margin: 5% 0;
}

.appTodoContainer {
  flex: 1 1;
  height: 75%;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.input {
    height: 50px;
    width: 80%;
    border-radius: 12px;
    background-color: aliceblue;
    display: flex;
    justify-content: center;
    align-items: center;
}

input {
    background: none;
    border: none;
    margin-left: 12px;
    flex: 1 1;
}

input:focus {
    outline: none;
}

button {
    margin-right: 8px;
    background-color: rgba(214, 213, 213, 0.75);
    border: none;
    border-radius: 10px;
    height: 30px;
    width: 50px;
}
.todoItem {
    display: flex;
    align-items: center;
    width: 80%;
    background-color: white;
    margin: 6px 4px;
    height: 40px;
    border-radius: 12px;
}

.todoItem>p {
    flex: 1 1;
}

.todoItem--done {
    text-decoration: line-through;
}
